<template>
  <div class="user">
    <div class="user-tools">
      <el-button type="primary" @click="SHOW_USER_ADD = true">新增会员</el-button>
      <el-button type="primary" @click="SHOW_USER_ALL = true">编号总览</el-button>
      <el-button type="success" icon="el-icon-search" circle style="float: right;" @click="handleSearchToggle"></el-button>
    </div>
    <transition name="user-search">
      <div class="user-search" v-show="SHOW_SEARCH">
        <el-form
          :inline="true"
          :model="searchForm"
          size="small"
        >
          <el-form-item label="状态">
            <el-select v-model="searchForm.userState">
              <el-option label="未领取" value="未领取"></el-option>
              <el-option label="未贴标" value="未贴标"></el-option>
              <el-option label="正常" value="正常"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="会员编号">
            <el-input v-model="searchForm.id"></el-input>
          </el-form-item>
          <el-form-item label="姓名">
            <el-input v-model="searchForm.realName"></el-input>
          </el-form-item>
          <el-form-item label="昵称">
            <el-input v-model="searchForm.wxName"></el-input>
          </el-form-item>
          <el-form-item label="车型">
            <el-input v-model="searchForm.carType"></el-input>
          </el-form-item>
          <el-form-item label="车色">
            <el-select v-model="searchForm.carColor">
              <el-option
                v-for="(item, index) in carColorOptions"
                :key="index"
                :label="item.label"
                :value="item.label">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="车牌号">
            <el-input v-model="searchForm.carNo"></el-input>
          </el-form-item>
          <el-form-item label="电话">
            <el-input v-model="searchForm.phone"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="handleSearchSubmit">搜索</el-button>
            <el-button type="info" @click="handleSearchClear">清空</el-button>
          </el-form-item>
        </el-form>
      </div>
    </transition>
    <el-table
      :data="userCache"
      :max-height="tableHeight"
      border
      class="user-table"
      v-loading="LOADING_USER_TABLE">
      <el-table-column
        label="状态"
        prop="state">
      </el-table-column>
      <el-table-column
        label="会员编号"
        prop="id">
      </el-table-column>
      <el-table-column
        label="姓名/昵称">
        <template slot-scope="scope">
          {{`${scope.row.realName}（${scope.row.wxName}）`}}
        </template>
      </el-table-column>
      <el-table-column
        label="车型">
        <template slot-scope="scope">
          {{`${scope.row.carType}-${scope.row.carType2}`}}
        </template>
      </el-table-column>
      <el-table-column
        label="车牌号"
        prop="carNo">
      </el-table-column>
      <el-table-column
        label="电话"
        prop="phone">
      </el-table-column>
      <el-table-column
        label="操作">
        <template>
          <i class="el-icon-edit" title="编辑"></i>
<!--          <i class="el-icon-delete" title="删除"></i>-->
<!--          <i class="el-icon-circle-close" title="拉黑"></i>-->
        </template>
      </el-table-column>
    </el-table>

    <el-dialog
      title="新增会员"
      :visible.sync="SHOW_USER_ADD"
      v-if="SHOW_USER_ADD"
      :close-on-click-modal="false"
      width="60%">
      <user-add ref="userAdd"></user-add>
      <span slot="footer" class="dialog-footer">
        <el-button @click="SHOW_USER_ADD = false">取 消</el-button>
        <el-button @click="handleUserAddSubmit" type="primary" :loading="LOADING_USER_ADD">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="编号总览"
      :visible.sync="SHOW_USER_ALL"
      v-if="SHOW_USER_ALL"
      fullscreen>
      <user-all :user="user"></user-all>
    </el-dialog>
  </div>
</template>

<script>
import $axios from '@/plugins/ajax';
import UserAdd from './dialog/UserAdd';
import UserAll from './dialog/UserAll';

export default {
  components: {
    UserAdd,
    UserAll
  },

  data() {
    return {
      user: [],
      userCache: [],

      SHOW_SEARCH: false,
      searchForm: {
        userState: '',
        id: '',
        realName: '',
        wxName: '',
        carType: '',
        carColor: '',
        carNo: '',
        phone: ''
      },

      tableHeight: 0,

      SHOW_USER_ADD: false,
      LOADING_USER_ADD: false,
      LOADING_USER_TABLE: false,

      SHOW_USER_ALL: false
    };
  },

  computed: {
    carColorOptions() {
      return this.$store.state.carColorOptions;
    }
  },

  created() {
    this.getList();
  },

  mounted() {
    this.tableHeight = document.getElementById('homeRight').clientHeight - 150;
  },

  methods: {
    getList() {
      this.LOADING_USER_TABLE = true;
      $axios.get('/userList').then((res) => {
        if (res.data.result === true) {
          this.user = res.data.userList;
          this.userCache = JSON.parse(JSON.stringify(res.data.userList));
        } else {
          this.$message.error(res.data.message);
        }
        this.LOADING_USER_TABLE = false;
      }).catch((e) => {
        this.$message.error('查询会员列表失败');
        this.LOADING_USER_TABLE = false;
      });
    },

    handleUserAddSubmit() {
      this.LOADING_USER_ADD = true;
      this.$refs['userAdd'].postData().then(() => {
        this.LOADING_USER_ADD = false;
        this.SHOW_USER_ADD = false;
        this.getList();
      }).catch(() => {
        this.LOADING_USER_ADD = false;
      });
    },

    handleSearchToggle() {
      this.SHOW_SEARCH = !this.SHOW_SEARCH;
      if (this.SHOW_SEARCH === true) {
        this.tableHeight -= 115;
      } else {
        this.tableHeight += 115;
      }
    },

    handleSearchSubmit() {
      this.LOADING_USER_TABLE = true;
      let arr = [];
      for (let i = 0; i < this.user.length; i++) {
        if (this.user[i].state.indexOf(this.searchForm.userState) > -1 &&
            this.user[i].id.indexOf(this.searchForm.id) > -1 &&
            this.user[i].realName.indexOf(this.searchForm.realName) > -1 &&
            this.user[i].wxName.indexOf(this.searchForm.wxName) > -1 &&
            `${this.user[i].carType}-${this.user[i].carType2}`.indexOf(this.searchForm.carType) > -1 &&
            this.user[i].carColor.indexOf(this.searchForm.carColor) > -1 &&
            this.user[i].carNo.indexOf(this.searchForm.carNo) > -1 &&
            this.user[i].phone.indexOf(this.searchForm.phone) > -1) {
          arr.push(this.user[i]);
        }
      }
      this.userCache = arr;
      this.LOADING_USER_TABLE = false;
    },

    handleSearchClear() {
      this.LOADING_USER_TABLE = true;
      this.userCache = JSON.parse(JSON.stringify(this.user));
      for (let key in this.searchForm) {
        this.searchForm[key] = '';
      }
      this.LOADING_USER_TABLE = false;
    }
  }
};
</script>

<style>
  .user-tools {
    margin-bottom: 10px;
  }
  .user-search {
    padding: 10px 10px 0 10px;
    border: 2px solid #ebeef5;
    border-radius: 10px;
    margin-bottom: 10px;
  }
  .user-search-enter-active {
    transition: opacity 1s;
  }
  .user-search-enter {
    opacity: 0;
  }
  .user-table [class^="el-icon-"] {
    font-size: 16px;
  }
  .user-table [class^="el-icon-"]+i {
    margin-left: 10px;
  }
</style>
